:root {
  --primary-color: #4c2b41;
  --secondary-color: #be5869;
  --main-bg-color: #4e4a4a;
  --input-bg-color: #5c5c5c;
  --input-bg-color-hover: #8d8d8d;
  --input-text-color: #fff;
  --input-placeholder-color: #cac2c2;
  --title-color: #2b2b2b;
  --button-bg-color: transparent;
  --button-color: #cac2c2;
  --button-color-hover: #323232;
  --task-bg-color: #7a7a7a;
  --task-name-color: #cac2c2;
}

* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 20px;
}

.main {
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.main__box {
  background-color: var(--main-bg-color);
  border-radius: 2.5rem;
  width: 80vh;
  padding: 1rem;
}

.main__box--top {
  text-align: center;
}

.main__box--top-title {
  opacity: .7;
  color: var(--title-color);
  margin: 2rem 0;
  font-size: 3.5rem;
}

.main__box--top-form {
  border-radius: 4rem;
  width: 90%;
  margin: 0 auto;
}

.main__box--top-input {
  background-color: var(--input-bg-color);
  color: var(--input-text-color);
  border: none;
  border-radius: 2rem;
  outline: red;
  width: 80%;
  height: 6rem;
  margin: 2rem 0;
  padding: 0 2rem;
  font-size: 2rem;
  transition: all .3s;
}

.main__box--top-input-hidden {
  background-color: var(--input-bg-color-hover);
  color: var(--input-text-color);
  border: none;
  border-radius: 2rem;
  outline: red;
  width: 40%;
  height: 6rem;
  margin: 2rem 0;
  padding: 0 1rem;
  font-size: 2rem;
  transition: all .3s;
}

.main__box--top-input::placeholder {
  color: var(--input-placeholder-color);
  opacity: 1;
  font-size: 2rem;
  transition: all .3s;
}

.main__box--top-input:focus::placeholder {
  opacity: 0;
  font-size: 2rem;
}

.main__box--top-submit {
  background-color: var(--button-bg-color);
  color: var(--button-color);
  cursor: pointer;
  border: none;
  border-radius: 2rem;
  width: 15%;
  height: 4rem;
  font-size: 2rem;
  transition: all .3s;
}

.main__box--top-submit:hover {
  transform: scale(1.2);
}

.main__box--bottom {
  text-align: center;
}

.main__box--bottom-title {
  color: var(--title-color);
  opacity: .7;
  margin: 3rem 0;
  font-size: 3rem;
}

.main__box--bottom-task {
  background-color: var(--task-bg-color);
  border-radius: 2rem;
  align-items: center;
  width: 100%;
  min-height: 6rem;
  margin: 1rem auto;
  display: flex;
}

.main__box--bottom-name {
  color: var(--task-name-color);
  opacity: 1;
  text-align: left;
  width: 75%;
  overflow-wrap: break-word;
  inline-size: 95rem;
  padding-left: 2rem;
  font-size: 1.8rem;
  transition: all .3s;
}

.main__box--bottom-buttons {
  border-radius: 2rem;
  width: 25%;
  margin-right: 2rem;
}

.main__box--bottom-edit {
  background-color: var(--button-bg-color);
  color: var(--button-color-hover);
  cursor: pointer;
  border: none;
  border-radius: 2rem;
  height: 4rem;
  margin: 0 1rem;
  padding: 0 1rem;
  font-size: 2rem;
  transition: all .3s;
}

.main__box--bottom-edit:hover {
  transform: scale(1.2);
}

.main__box--bottom-delete {
  background-color: var(--button-bg-color);
  color: var(--button-color-hover);
  cursor: pointer;
  border: none;
  border-radius: 2rem;
  height: 4rem;
  margin: 0 1rem;
  padding: 0 1rem;
  font-size: 2rem;
  transition: all .3s;
}

.main__box--bottom-delete:hover {
  transform: scale(1.2);
}

